import React, { useState, useEffect, useContext, useRef } from "react";

import RoomVisualization from "@/Components/RoomMap/RoomMap";
import Room1 from "@/Components/RoomMap/RoomMap3";
import Room2 from "@/Components/RoomMap/RoomMap4";
import { RoomProvider } from "@/Context/RoomMapContext";
import { CustomContext } from "@/Context/UseCustomContext";
import useWebSocket from "@/hook/useWebSoket";
import { decodePosition } from "@/utils/helper";
import { FaUser } from "react-icons/fa6";
import { Tooltip } from "antd";
export default function LiveMapTab() {
  const context = useContext(CustomContext);
  const { elderlyDetails } = context || {};
  const deviceCode = elderlyDetails?.deviceId;
  // Update handleDecode to normalize and validate position
  const [position, setPosition] = useState([]);
  const [heartBreathRateActive, setHeartBreathRateActive] = useState(false);

  const [peopleData, setPeopleData] = useState(null);
  const heartBreathRateRef = useRef(false);
  const timeoutRef = useRef;
  function handleDecode(code) {
    const data = decodePosition(code);
    console.log(data);

    const modifiedPeopleData = [...data];

    if (
      modifiedPeopleData[0]?.postureIndex === 4 &&
      heartBreathRateRef.current
    ) {
      // Modify the first person (index 0) posture
      modifiedPeopleData[0].postureIndex = 6;
      modifiedPeopleData[0].posture = "Resident on Bed";
      modifiedPeopleData[0].color = "#252F67";
    }

    setPeopleData(modifiedPeopleData);
    setPosition(modifiedPeopleData);
  }
  function handleHeartBreathUpdate() {
    setHeartBreathRateActive(true);
    heartBreathRateRef.current = true;

    clearTimeout(timeoutRef.current); // Clear any existing timeout
    timeoutRef.current = setTimeout(() => {
      setHeartBreathRateActive(false);
      heartBreathRateRef.current = false;
    }, 5000);
  }
  useWebSocket({
    onMessage: (data) => {
      const payload = JSON.parse(data?.message)?.payload;
      if (
        payload?.deviceCode === deviceCode &&
        (payload?.position || payload?.heartbreath)
      ) {
        if (payload?.position) {
          handleDecode(payload.position);
        }
        if (payload?.heartbreath) {
          handleHeartBreathUpdate();
        }
      }
    },
    dependencies: [deviceCode],
  });
  return (
    <>
      <div className="flex flex-col gap-4 mt-6 overflow-hidden">
        {elderlyDetails?.rooms &&
          elderlyDetails?.rooms
            ?.filter((room) => room.device_no)
            .map((room, indx) => (
              <div
                key={indx}
                className="flex flex-col gap-4 bg-white rounded-2xl p-4 h-[700px]"
              >
                <h1 className="text-2xl font-bold">{room?.name}</h1>
                <div className="flex h-full">
                  <div className="top-2 left-2  border-2 p-0 rounded-full flex items-center gap-1 pr-2 h-fit w-fit z-50">
                    {peopleData?.length > 0 && (
                      <Tooltip
                        color="white"
                        overlayInnerStyle={{ color: "black", padding: "10px" }}
                        title={peopleData?.map((people, indx) => (
                          <div
                            key={indx}
                            className="flex items-center gap-2 mb-2"
                          >
                            <div
                              style={{ backgroundColor: people?.color }}
                              className=" p-2 rounded-full"
                            >
                              <FaUser size={14} color="white" />
                            </div>
                            <div>
                              <p className="text-sm m-0">{people?.posture}</p>
                            </div>
                          </div>
                        ))}
                      >
                        <div className="flex -space-x-4 cursor-pointer">
                          {peopleData?.map((people, indx) => (
                            <div
                              key={indx}
                              style={{ backgroundColor: people?.color }}
                              className={` p-1 rounded-full border-2 border-white`}
                            >
                              <FaUser size={16} color="white" />
                            </div>
                          ))}
                        </div>
                      </Tooltip>
                    )}

                    {peopleData?.length === 0 && (
                      <div
                        className={`bg-primary p-1 rounded-full border-2 border-white`}
                      >
                        <FaUser size={16} color="white" />
                      </div>
                    )}
                    <span className="font-bold text-lg">
                      {peopleData?.length}
                    </span>
                  </div>
                  <div id="roomMap" className="w-full h-full scale-[1.2] mt-14">
                    {room?.mount_type === 2 ? (
                      <Room2 roomInfo={room} position={position} />
                    ) : (
                      <Room1 roomInfo={room} position={position} />
                    )}
                  </div>
                </div>
              </div>
            ))}
      </div>
    </>
  );
}
