import React, { createContext, useContext, useState, useMemo } from "react";

// Units (conversion constants)
const units = {
  cm: 1,
  meter: 100, // 1 meter = 100 cm
  feet: 30.48, // 1 foot = 30.48 cm
};

// Global Configuration Context
export const RoomContext = createContext();

// Hook to use RoomContext values
export const useRoomContext = () => useContext(RoomContext);

// Provider for RoomContext
export const RoomProvider = ({ children }) => {
  const [unit, setUnit] = useState("cm");
  const [coverageArea, setCoverageArea] = useState({
    x: 600, // in cm
    y: 400, // in cm
  });
  const [roomDimensions, setRoomDimensions] = useState({
    x: 700, // in cm
    y: 500, // in cm
  });
  const [devider, setDevider] = useState(1);
  const [cmToPixel, setCmToPixel] = useState(1 / devider);
  const convertToUnit = (valueInCm) => {
    return valueInCm / units[unit];
  };

  const contextValue = useMemo(
    () => ({
      coverageArea,
      unit,
      setUnit,
      setCoverageArea,
      convertToUnit,
      units,
      cmToPixel,
      setCmToPixel,
      roomDimensions,
      setRoomDimensions,
      devider,
      setDevider,
    }),
    [unit, coverageArea, cmToPixel, roomDimensions, devider]
  );

  return (
    <RoomContext.Provider value={contextValue}>{children}</RoomContext.Provider>
  );
};
